.title {
  margin-bottom: 35px;
  color: #201f1f;
  font-family: Poppins;
  font-size: 75px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -1px;
  text-align: center;
}
.title.dark-theme {
  color: #ffffff;
}

.title span {
  color: #5c4eae;
}

.title-recipe {
  font-size: 45px;
  line-height: 100%;
  margin-top: 30px;
  text-align: center;
}

.home {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.home-text {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.home-descr {
  display: flex;
  align-items: center;
  color: #676767;
}

.underline {
  background: #676767;
  width: 54px;
  height: 1px;
  margin-right: 10px;
}

.subtitle-home {
  max-width: 350px;
  color: #676767;
  padding-bottom: 20px;
}

.home-images {
  position: relative;
  width: 422px;
  height: 424px;
  flex-shrink: 0;
  border-radius: 424px;
  background: linear-gradient(137deg, #5c62ec 4.83%, #5144a0 98.22%);
}

.home-images img {
  position: absolute;
  top: 2%;
  left: -4%;
}

@media (max-width: 800px) {
  .home {
    flex-direction: column;
  }
}

@media (max-width: 550px) {
  .home-images {
    display: none;
  }

  .title {
    font-size: 60px;
  }
}
